.link {
	display: inline-block;
	text-decoration: none;
	cursor: pointer;
	outline: none;
	border: none;
	vertical-align: top;
	color: @c-text;
}
.container {
	max-width: 82.813vw;
	margin: 0 auto;
	padding: 0 0.8vw;
	@media(max-width: 767px) {
		max-width: 90vw;
	}
}
.btn {
	position: relative;
	padding: 1.2vw;
	font-size: 0.8vw;
	font-weight: 500;
	cursor:pointer;
	border: none;
	outline: none	;
	border-radius: 30px;
	background-color: #fefcfb;
	box-shadow: inset 0 -4px 1px #ebeaea;
	transition: all 0.3s;
	@media(max-width: 992px) {
		padding: 18px;
		font-size: 14px;
	}
	&:hover {
		background-color:lighten(#fefcfb, 1%);
		box-shadow: inset 0 -4px 1px lighten(#ebeaea, 1%);
	}
	&-big {
		width: 19.79vw;
		padding: 1.4vw;
		padding-right: 8.073vw;
		padding-left: 2.344vw;
		border-radius: 45px;
		@media(max-width: 992px) {
			width: 270px;
			padding: 20px;
			padding-right: 55px;
			padding-left: 30px;
		}
		@media(max-width: 450px) {
			padding: 15px;
			padding-left: 25px;
			padding-right: 75px;
		}
	}
	&-img-right {
		position: relative;
	}

	&-icon {
		padding-left: 2.865vw;
		@media(max-width: 992px) {
			padding-left: 45px;
		}
	}
	&--bg-red {
		color: #fff;
		text-shadow: 0 1px 5px rgba(210, 110, 116, 0.49);
		background-color: #f1979b;
		box-shadow: inset 0 -4px 1px #d7747a,0 7px 27px rgba(241, 131, 131, 0.57);
		&:hover {
			background-color:darken(#f1979b, 1%);
			box-shadow: inset 0 -4px 1px darken(rgba(241, 131, 131, 0.57), 1%),0 7px 27px rgba(241, 131, 131, 0.57);
		}
		
	}
}
.btn-img {
	position: absolute;
	top: 0;
	right: 0;
}
.title {
	//font-size: 48px;
	.fz48();
	.fw700();
	line-height: 1.1;
	span {
		.fw300();
	}
}
.icon {
	display: flex;
	justify-content: center;
	align-items: center;
	width: 1.823vw;
	height: 1.823vw;
	margin-right: 0.625vw;
	padding: 0.5vw;
	border-radius: 50%;
	background-color: #fff;
	@media(max-width: 992px) {
		width: 25px;
		height: 25px;
		padding: 6px;
	}
}
.icon-btn {
	position: absolute;
	left: 1.1vw;
	top: 50%;
	transform: translateY(-50%);
	width: 1.142vw;
	height: 1.142vw;
	padding: 0.1vw;
	@media(max-width: 992px) {
		left: 20px;
		width: 20px;
		height: 20px;
		padding: 2px;
	}
}
.df {
	display: flex;
}
