}
.ttu {
	text-transform: uppercase;
}
.overlay {
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	background-color: rgba(255, 255, 255, .9);
	z-index: 100;
}
.hidden {
	display: none;
}