* {
	box-sizing: border-box;
}
html {
	font-size: 10px;
}
body {
	position: relative;
	min-width: 320px;
	margin: 0;
	font-family: @ff;
	font-weight: normal;
	color: @c-text;
	line-height: 1.2;
	overflow-x: hidden;
}
h1, h2, h3, h4, h5, h6, ul, p {
	padding: 0;
	margin: 0;
	font-weight: normal;
}
ul {
	padding: 0;
	margin: 0;
	list-style-type: none;
}
.link {
	display: inline-block;
	text-decoration: none;
	cursor: pointer;
	outline: none;
	border: none;
	vertical-align: top;
	color: @c-text;
}
