.hero-bg {
	overflow: hidden;
}
.header {
	position: relative;
	padding-top: 1.56vw;
	&__top-line {
		display: flex;
		justify-content: space-between;
		align-items: center;
		padding: 0 0.8vw;
		@media(max-width: 600px) {
			flex-wrap: wrap;
		}
	}
	&__map {
		display: none;
		position: absolute;
		top: 100%;
		left: 0;
		border: none;
		opacity: 0;
		z-index: 5;
		transition: all 0.4s;
	} 
	&__open-map {
		position: relative;
		display: inline-block;
		margin-top: 0.885vw;
		color: #ff6078;
		font-size: 0.625vw;
		letter-spacing: 1.2px;
		cursor: pointer;
		&:hover>.header__map {
			display: block;
			opacity: 1;
			transition: all 0.4s;
		}
		@media(max-width: 992px) {
			margin-top: 5px;
		}
		&::after {
			content: '';
			position: absolute;
			top: 105%;
			width: 100%;
			height: 2px;
			left: 50%;
			transform: translateX(-50%);
			background:url('@{bg}header/map-underline.png') no-repeat left center/cover;
		}
	}
	&__item-phone {
		@media(max-width: 600px) {
			width: 100%;
			margin-top: 10px;
			order: 3;
		}
	}
}	
.logo {
	width: 14.583vw;
	height: 6.563vw;
	@media(max-width: 992px) {
		position: relative;
		width: 145px;
		height: auto;
		z-index: 51;
	}
	@media(max-width: 767px) {
		width: 135px;
	}
	@media(max-width: 600px) {
		order: 1;
	}
	
}
.location {
	display: flex;
	align-items: center;
}
.location-img {
	width: 4.062vw;
	height: 4.062vw;
	margin-right: 1.04vw;
	border-radius: 50%;
	box-shadow: 0 3px 20px rgba(68, 68, 68, 0.11);
	img {
		width: 100%;
		height: auto;
	}
	@media(max-width: 992px) {
		width: 60px;
		height: 60px;
	}
}
.adress {
	//margin-top: 0.26vw;
}
.phone {
	display: flex;
	align-items: center;
	&__item:hover {
		color: lighten(@c-text,25%);
	}
	.circle {
		display: inline-block;
		width: 0.313vw;
		height: 0.313vw;
		margin-right: 0.3vw;
		border-radius: 50%;
		box-shadow: 0 0 7px rgba(79, 160, 55, 0.61);
		background-color: #27b24b;
		@media(max-width: 992px) {
			width: 6px;
			height: 6px;
			padding: 0;
		}
	}
}
.working-time {
	display: flex;
	align-items: center;
	margin-top: 0.26vw;
}
.header-nav {
	margin-top: 0.8vw;
	padding-bottom: 0.8vw;
}
.nav {
	position: relative;
	display: flex;
	justify-content: space-between;
	align-items: flex-start;
	padding: 0 1.302vw;
	border-radius: 5px;
	background-color: #fff;
	@media(max-width:992px) {
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: 100vh;
		flex-direction: column;
		align-items: center;
		justify-content: flex-start;
		padding-top: 70px;
		background-color: #ebeaea;
		overflow: auto;
		z-index: 50;
	}

	&::after {
		content: '';
		position: absolute;
		top: 100%;
		left: 50%;
		transform: translateX(-50%);
		width: 97%;
		height: 0.417vw;
		border-bottom-left-radius: 5px;
		border-bottom-right-radius: 5px;
		background-color: rgba(255,255,255,.5);
		@media(max-width:992px) {
			position: fixed;
			top: 0;
			left: 0;
			transform: translateX(0);
			height: 80px;
			background-color: #ebeaea;
			z-index: 1;
		}
	}
	.link {
		padding: 1.042vw 0.521vw;
		@media(max-width:992px) {
			font-size: 16px;
			padding: 10px;
		}
		&:hover {
			transform: scale(1.01);
		}
	}
	.active-menu-item {
		font-weight: 700;
		&:hover {
			transform: scale(1);
		}
	}
}
.bottom-img {
	position: absolute;
	left: 0;
	bottom: 0;
	width: 100%;
	height: 8.02vw;
	background: url('@{bg}botom-img.png') no-repeat top center/cover;
	@media(max-width:992px) {
		height: 80px;
	}
}
.modal-form {
