		text-align: center;
		z-index: 1;
	}
	&__btn {
		position: relative;
		display: block;
		width: 25.521vw;
		margin: 0 auto;
		padding: 31/@vw;
		padding-left: 70/@vw;
		text-align: left;
		z-index: 1;
		@media(max-width:992px) {
			width: 80%;
			padding-left: 60px;
		}
		@media(max-width:600px) {
			padding-left: 35px;
		}
		@media(max-width:450px) {
			width: 95%;
			padding-right: 50px;
		}
	}
	&__btn-img {
		width: 10.417vw;
		height: 2.708vw;
		top: 10/@vw;
		right: -65/@vw;
		@media(max-width:992px) {
			width: 170px;
			height: 45px;
			top: 5px;
			right: -65px;
		}
		@media(max-width:600px) {
			width: 140px;
			height: 35px;
			top: 8px;
			right: -55px;
		}
		@media(max-width: 450px) {
			width: 120px;
			height: 30px;
			top: 5px;
			right: -45px;
		}
		
	}
	&__img-bg {
		position: absolute;
		left: -110/19.2vw;
		top: 195/19.2vw;
		width: 263/@vw;
		height: 386/@vw;
		@media(max-width:992px) {
			top: 160px;
			left: -50px;
			width: 200px;
			height: 300px;
		}
		@media(max-width: 767px) {
			top: 145px;
			left: -30px;
			width: 130px;
			height: 195px;
		}
		@media(max-width: 450px) {
			display: none;
		}
