}
.select,
input {
	position: relative;
	display: block;
	//width: 100%;
	width: 26.042vw;
	margin: 0 auto;
	margin-bottom: 15px;
	// padding: 29px 45px;
	// padding-right: 60px;
	padding: 1.51vw 2.344vw;
	padding-right: 3.125vw;
	.fw300();
	.fz18();
	color: #989797;
	outline: none;
	border-radius: 40px;
	border: 1px solid #fbeeef;
	background-color: #ffffff;
	@media(max-width:992px) {
		width: 80%;
	}
	@media(max-width: 450px) {
		width: 95%;
	}
	&::placeholder {
		color: #989797;
		.fw300();
		.fz18();
		line-height: 24px;
		letter-spacing: 0.45px;
	}
}
.select {
	cursor: pointer;
	&:hover>.select__list {
		display: block;
	}
	&:hover>.arrow {
		transform: translateY(-50%) rotate(180deg);
	}
	&__list {
		display: none;
		position: absolute;
		top: 100%;
		left: 0;
		width: 100%;
		padding: 25px 45px;
		border-radius: 20px;
		border: 1px solid #fbeeef;
		background-color: #ffffff;
		z-index: 2;
	}
	&__option {
		.fw300();
		.fz18();
		color: #989797;
		cursor: pointer;
		&:hover {
			color: @c-text;
		}
	}
}
.arrow {
	position: absolute;
	top: 50%;
	right: 45/@vw;
	transform: translateY(-50%);
	display: block;
	width: 18/@vw;
	height: 10/@vw;
	@media(max-width:992px) {
		width: 11px;
		height: 8px;
		right: 20px;
	}
	img {
		position: absolute;
		top: 0;
		left: 0;
	}
	
}
.close {
	position: absolute;
	top: 2.5vw;
	right: 2.083vw;
	cursor: pointer;
	transition: all 0.3s;
	@media(max-width:992px) {
		top: 25px;
		right: 30px;
	}
	@media(max-width:450px) {
		top: 10px;
		right: 15px;
	}
	&:hover {
		transform: scale(1.2);
	}
}
.modal-send {
	position: fixed;
	top: 50vh;
	left: 50vw;
	transform: translate(-50%, -50%);
	padding: 75/@vw 120/@vw;
	border-radius: 50/@vw;
	border: 1px solid #fbeeef;
	background: #fff url('@{bg}header/modal-bg.png') no-repeat center center/cover;
	z-index: 101;
	@media(max-width:767px) {
		padding: 40px 55px;
	}
	@media(max-width:600px) {
		width: 250px;
		border-radius: 20px;
	}
	&__img {
		width: 91/@vw;
		height: 91/@vw;
		margin: 0 auto;
		@media(max-width:767px) {
			width: 45px;
			height: 45px;
		}
	}
	
	&__text {
		letter-spacing: 0.75px;
		span {
			font-weight: 300;
		}
	}
	&__bg-img {
		position: absolute;
		bottom: -4/@vw;
		right: -2/@vw;
		width: 118/@vw;
		height: 189/@vw;
		@media(max-width:600px) {
			bottom: -2px;
			right: -3px;
			width: 40px;
			height: 60px;
		}
	}
}
.bottom-img {
	.item1 {
		animation: bounce  3s linear infinite;
	}
	.item2 {
		animation: bounce  3s linear infinite;
		animation-delay: 1s;
	}
	.item3 {
		animation: bounce  3s linear infinite;
		animation-delay: 2s;
	}
}
.circlr-bounce {
	position: absolute;
	top: -14/@vw;
	left: 50%;
	transform: translateX(-50%);
	width: 60/@vw;
	height: 60/@vw;
	border: 1px solid #fff;
	border-radius: 50%;
	@media(max-width:992px) {
		width: 60px;
		height: 60px;
	}
}
.show {
	animation: show  0.6s linear forwards;
}
.show-h {
	animation: showh  0.2s linear forwards;
}
@keyframes bounce {
	from {
		opacity: 1;
		transform: translateX(-50%) scale(1);
	}
	to{
		opacity: 0;
		transform: translateX(-50%) scale(1.9);
	}
};
@keyframes pulse {
  from {
    box-shadow: 0 0 0 0 rgba(239, 148, 153, 0.5);
  }
  to {
      box-shadow: 0 0 0 10px rgba(239, 148, 153, 0);
  }
}
@keyframes show {
	from {
		opacity: 0;
	}
	to{
		opacity: 1;
	}
}
@keyframes showh {
	from {
		opacity: 1;
	}
	to{
		opacity: 0;
	}
}