.hero {
	position: relative;
	padding-bottom: 100/@vw;
	@media(max-width: 992px) {
		padding-bottom: 100px;
	}
	&__container {
		position: relative;
		padding: 5.99vw;
		@media(max-width: 992px) {
			padding: 0;
			margin-top: 35px;
		}
	}
	&__content {
		position: relative;
		width: 62%;
		z-index: 1;
		@media(max-width: 992px) {
			width: 100%;
		}
	}
	&__img {
		position: absolute;
		right: -9.375vw;
		bottom: -1.042vw;
		display: flex;
		justify-content: flex-end;
		align-items: flex-end;
		width: 45.21vw;
		height: 39.583vw;
	}
	&__list {
		margin-top: 2.865vw;
		@media(max-width: 992px) {
			margin-top: 60px;
		}
		@media(max-width: 450px) {
			margin-top: 40px;
		}
		li {
			display: flex;
			align-items: center;
		}
	}
	&__btn-img {
		top: 0.26vw;
		right: -5.808vw;
		width: 14.219vw;
		height: 3.854vw;
		@media(max-width: 992px) {
			top: 50%;
			right: -75px;
			transform: translateY(-50%);
			width: 140px;
			height: 38px;
		}
		@media(max-width: 450px) {
			right: -20px;
			width: 100px;
			height: 25px;
		}
	}
}
.red-circle {
	width: 0.885vw;
	height: 0.885vw;
	margin-right: 0.73vw;
	font-size: 0;
	border-radius: 50%;
	border: 0.23vw solid #f7f7fb;
	background-color: #ef9499;
	box-shadow: 0 1px 13px rgba(239, 148, 153, 0.38);
	@media(max-width: 992px) {
		width: 10px;
		height: 10px;
		border: 2px solid #f7f7fb;
	}
}
.card {
	position: absolute;
	width: 853/@vw;
	height: 521/@vw;
	left: 410/@vw;
	bottom: -235/@vw;
	@media(max-width: 992px) {
		width: 575px;
		height: 280px;
		// width: 150px;
		// height: 200px;
		left: 315px;
		bottom: -10px;
	}
	@media(max-width: 650px) {
		display: none;
	}
	&__img {
		// width: 176/@vw;
		// height: 236/@vw;
		img {
			width: 100%;
			height: auto;
		}
	}
	&__heart {
		position: absolute;
		top: 15/@vw;
		left: 285/@vw;
		// top: 0.8vw;
		// right: -3.385vw;
		width: 6.771vw;
		height: 6.51vw;
		@media(max-width: 992px) {
			width: 114px;
			height: 95px;
			left: 185px;
			top: 5px;
		}
	}
	&__info {
		position: absolute;
		top: 185/@vw;
		left: 325/@vw;
		// top: 9.635vw;
		// right: -0.8vw;
		width: 2.083vw;
		height: 2.083vw;
		padding: 0.5vw;
		cursor: pointer;
		border-radius: 50%;
		background-color: #ff9999;
		box-shadow: inset -2px -1px 1px #d7747a;
		z-index: 1;
		animation: pulse  1.5s infinite;
		@media(max-width: 992px) {
			width: 35px;
			height: 35px;
			left: 212px;
			top: 125px;
			padding: 8px;
		}
	}
	&__text {
		position: absolute;
		top: 0;
		left: 347/@vw;
		// top: -0.364vw;
		// left: 8.854vw;
		width: 9.166vw;
		height: 10.937vw;
		padding: 1.563vw 1.302vw;
		text-align: center;
		letter-spacing: 1.68px;
		border-top-left-radius: 100px  20px;
		border-top-right-radius: 100px  20px;
		border-bottom-left-radius: 12px;
		border-bottom-right-radius: 10px;
		overflow: hidden;
		background-color: #fffefd;
		box-shadow: 10px -14px rgba(255, 255, 255, 0.4),0 2px 18px rgba(187, 194, 206, 0.32);
		@media(max-width: 992px) {
			top: -65px;
    	left: 48px;
			width: 176px;
			height: 210px;
			padding: 30px 20px;
			box-shadow: -10px -14px rgba(255, 255, 255, 0.4),0 2px 18px rgba(187, 194, 206, 0.32);
		}
		p {
			width: 6.615vw;
			height: 7.552vw;
			overflow: hidden;
			@media(max-width: 992px) {
				width: 127px;
				height: 150px;
			}
		}
	}
}
.down {
	position: absolute;
	left: 50%;
	transform: translate(-50%,20%);
	top: 0;
	// width: 131px;
	// height: 131px;
	width: 32/@vw;
	height: 64/@vw;
	cursor: pointer;
	outline: none;
	border: none;
	background: url('@{bg}hero/button-down.png') no-repeat center center/contain;
	z-index: 1;
	transition: all 0.2s;
	@media(max-width:992px) {
		width: 40px;
		height: 40px;
	}
	&:hover {
		transform: translate(-50%,20%) scale(1.05);
	}
}